#navbar-container {
    padding: 0px 0px;
    margin: 0px 0px;
}

#navbar-button {
    border-width: 2px;
    border-color: #fcffff;
}

#navbar-icon {
    color: #fcffff;
}

#navbar-link {
    transition:
        transform 0.15s linear 0s,
        text-shadow 0.15s linear 0s;
}

#navbar-link:hover {
    transform: translate(-3px, -3px);
    text-shadow: 4px 4px #00000044;
}

#navbar-link.active {
    color: #bccdaf !important;
    transform: translate(-3px, -3px);
    text-shadow: 4px 4px #00000044;
}